'use client';

import React, { useEffect, useState, useRef } from 'react';
import { useRouter } from 'next/navigation';
import { useAppContext } from '../context/AppContext';
import { signInWithGoogle } from '../lib/supabase/auth';
import LoginModal from './LoginModal';


// Import header styles
import {
  SharedHeader,
  HeaderContainer,
  HeaderLogo,
  HeaderMenu,
  HeaderLink,
  HeaderIcons,
  CartCount,
  NavIcon,
  MobileMenuToggle
} from '../styles/HeaderStyles';

interface SharedHeaderProps {
  activePage?: string;
}

const Header: React.FC<SharedHeaderProps> = ({ activePage = '' }) => {
  const router = useRouter();
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isAuthModalOpen, setIsAuthModalOpen] = useState(false);
  const [isAccountModalOpen, setIsAccountModalOpen] = useState(false);
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const [dropdownTimeout, setDropdownTimeout] = useState<NodeJS.Timeout | null>(null);
  const dropdownRef = useRef<HTMLDivElement>(null);

  const { user, cartItems, logout, loading } = useAppContext();
  const cartCount = cartItems.reduce((total, item) => total + item.quantity, 0);

  const navigate = (path: string) => {
    router.push(path);
    setIsMobileMenuOpen(false);
  };

  const handleLogout = () => {
    logout();
    navigate('/');
    setIsMobileMenuOpen(false);
  };

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  // Trigger Google sign-in by opening auth modal
  const handleGoogleSignIn = () => {
    setIsAuthModalOpen(true);
  };

  // Close auth modal
  const closeAuthModal = () => {
    setIsAuthModalOpen(false);
  };

  // Listen for showLoginModal event from other components
  useEffect(() => {
    const handleShowLoginModal = () => {
      setIsAuthModalOpen(true);
    };

    window.addEventListener('showLoginModal', handleShowLoginModal);
    return () => {
      window.removeEventListener('showLoginModal', handleShowLoginModal);
    };
  }, []);

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
        setIsDropdownOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  return (
    <>
      <header role="banner">
        <SharedHeader>
          <HeaderContainer>
            <HeaderLogo>
              <a href="#" onClick={(e) => { e.preventDefault(); navigate('/'); }} aria-label="Home">
                <img
                  src="/logo.svg"
                  alt="Colour My Space Logo"
                  className="logo-image"
                  style={{ height: '40px', width: 'auto', maxHeight: '40px' }}
                />
              </a>
            </HeaderLogo>

            <nav role="navigation" aria-label="Main navigation">
              <HeaderMenu>
                <HeaderLink href="#" $active={activePage === 'home'} onClick={(e) => { e.preventDefault(); navigate('/'); }}>
                  Home
                </HeaderLink>
                <HeaderLink href="#" $active={activePage === 'shop'} onClick={(e) => { e.preventDefault(); navigate('/shop'); }}>
                  Shop
                </HeaderLink>
                <HeaderLink href="#" $active={activePage === 'portfolio'} onClick={(e) => { e.preventDefault(); navigate('/portfolio'); }}>
                  Portfolio
                </HeaderLink>
                <HeaderLink href="#" $active={activePage === 'services'} onClick={(e) => { e.preventDefault(); navigate('/services'); }}>
                  Services
                </HeaderLink>

                {(user && (user.role === 'admin' || user.role === 'moderator')) && (
                  <HeaderLink href="#" $active={activePage === 'dashboard'} onClick={(e) => { e.preventDefault(); navigate('/dashboard'); }}>
                    Dashboard
                  </HeaderLink>
                )}
                <HeaderLink href="#" $active={activePage === 'about'} onClick={(e) => { e.preventDefault(); navigate('/about'); }}>
                  About
                </HeaderLink>
                <HeaderLink href="#" $active={activePage === 'contact'} onClick={(e) => { e.preventDefault(); navigate('/contact'); }}>
                  Contact
                </HeaderLink>
              </HeaderMenu>
            </nav>

            <HeaderIcons>
              <NavIcon onClick={() => {}} aria-label="Search">
                <i className="fas fa-search"></i>
              </NavIcon>
              
              {user ? (
                <div className="relative" ref={dropdownRef}>
                  <NavIcon onClick={() => setIsAccountModalOpen(true)}>
                    {user.avatar ? (
                      <img src={user.avatar} alt="Profile" className="w-6 h-6 rounded-full object-cover" />
                    ) : (
                      <i className="fas fa-user"></i>
                    )}
                  </NavIcon>

                  {/* Dropdown menu */}
                  <div
                    className={`absolute top-full right-0 mt-2 w-48 bg-white border border-gray-200 rounded-lg shadow-lg py-2 ${isDropdownOpen ? 'block' : 'hidden'}`}
                    onMouseEnter={() => {
                      if (dropdownTimeout) clearTimeout(dropdownTimeout);
                      setIsDropdownOpen(true);
                    }}
                    onMouseLeave={() => {
                      const timeout = setTimeout(() => setIsDropdownOpen(false), 200);
                      setDropdownTimeout(timeout);
                    }}
                  >
                    <div className="px-4 py-2 hover:bg-gray-50 cursor-pointer" onClick={() => { navigate('/account'); setIsDropdownOpen(false); }}>
                      View Account
                    </div>
                    {(user.role === 'admin' || user.role === 'moderator') && (
                      <div className="px-4 py-2 hover:bg-gray-50 cursor-pointer" onClick={() => { navigate('/moderator'); setIsDropdownOpen(false); }}>
                        Moderator
                      </div>
                    )}
                    <div className="px-4 py-2 hover:bg-gray-50 cursor-pointer" onClick={() => { handleLogout(); setIsDropdownOpen(false); }}>
                      Logout
                    </div>
                  </div>
                </div>
              ) : (
                <NavIcon onClick={handleGoogleSignIn} aria-label="Sign in to your account">
                  <i className="fas fa-user"></i>
                </NavIcon>
              )}

              {user && (
                <div style={{ position: 'relative' }}>
                  <NavIcon onClick={() => navigate('/cart')}>
                    <i className="fas fa-shopping-cart"></i>
                  </NavIcon>
                  {cartItems.length > 0 && <CartCount>{cartCount}</CartCount>}
                </div>
              )}

              <MobileMenuToggle onClick={toggleMobileMenu}>
                <span></span>
                <span></span>
                <span></span>
              </MobileMenuToggle>
            </HeaderIcons>
          </HeaderContainer>

          {/* Mobile Menu */}
          {isMobileMenuOpen && (
            <div className="md:hidden border-t border-gray-200 mt-4">
              <div className="px-4 py-2 space-y-1">
                <div className="py-2 px-4 hover:bg-gray-50 cursor-pointer rounded" onClick={() => navigate('/')}>Home</div>
                <div className="py-2 px-4 hover:bg-gray-50 cursor-pointer rounded" onClick={() => navigate('/shop')}>Shop</div>
                <div className="py-2 px-4 hover:bg-gray-50 cursor-pointer rounded" onClick={() => navigate('/portfolio')}>Portfolio</div>
                <div className="py-2 px-4 hover:bg-gray-50 cursor-pointer rounded" onClick={() => navigate('/services')}>Services</div>
                <div className="py-2 px-4 hover:bg-gray-50 cursor-pointer rounded" onClick={() => navigate('/booking')}>Book Consultation</div>
                {user && (user.role === 'admin' || user.role === 'moderator') && (
                  <div className="py-2 px-4 hover:bg-gray-50 cursor-pointer rounded" onClick={() => navigate('/dashboard')}>Dashboard</div>
                )}
                <div className="py-2 px-4 hover:bg-gray-50 cursor-pointer rounded" onClick={() => navigate('/about')}>About</div>
                <div className="py-2 px-4 hover:bg-gray-50 cursor-pointer rounded" onClick={() => navigate('/contact')}>Contact</div>
              </div>
            </div>
          )}
        </SharedHeader>
      </header>

      {/* Account Modal */}
      {user && isAccountModalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 backdrop-blur-sm p-4" onClick={() => setIsAccountModalOpen(false)}>
          <div className="w-full max-w-md rounded-2xl bg-white shadow-2xl overflow-hidden" onClick={(e) => e.stopPropagation()}>
            <div className="bg-white p-6">
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-2xl font-bold text-gray-900">Account Information</h2>
                <button
                  onClick={() => setIsAccountModalOpen(false)}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <svg className="h-6 w-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12"></path>
                  </svg>
                </button>
              </div>

              <div className="space-y-3 mb-6">
                <div className="flex items-center">
                  <div className="w-10 h-10 rounded-full bg-amber-100 flex items-center justify-center mr-3">
                    <span className="text-amber-700 font-semibold">
                      {user.name?.charAt(0)?.toUpperCase() || 'U'}
                    </span>
                  </div>
                  <div>
                    <p className="font-medium text-gray-900">{user.name}</p>
                    <p className="text-sm text-gray-600">{user.email}</p>
                  </div>
                </div>

                <div className="pt-2">
                  <p className="text-sm text-gray-600">
                    <span className="font-medium">Role:</span> <span className="capitalize">{user.role}</span>
                  </p>
                  <p className="text-sm text-gray-600">
                    <span className="font-medium">Member since:</span> {user.created_at ? new Date(user.created_at).getFullYear() : "N/A"}
                  </p>
                </div>
              </div>

              <div className="flex gap-3">
                <button
                  className="flex-1 px-4 py-2 bg-gray-200 text-gray-800 rounded-lg hover:bg-gray-300 transition-colors font-medium"
                  onClick={() => {
                    navigate("/account");
                    setIsAccountModalOpen(false);
                  }}
                >
                  View Profile
                </button>
                <button
                  className="flex-1 px-4 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors font-medium"
                  onClick={() => {
                    handleLogout();
                    setIsAccountModalOpen(false);
                  }}
                >
                  Logout
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Authentication Modal */}
      {isAuthModalOpen && (
        <div 
          className="fixed inset-0 z-[99999] flex items-center justify-center bg-black bg-opacity-75"
          onClick={closeAuthModal}
        >
          <div 
            className="relative bg-white rounded-2xl shadow-2xl max-w-sm w-full mx-4 overflow-hidden"
            onClick={(e) => e.stopPropagation()}
            style={{ transform: 'none', opacity: 1 }}
          >
            <div className="p-1 bg-gradient-to-r from-amber-400 via-amber-500 to-amber-600">
              <div className="bg-white rounded-xl p-6">
                <div className="flex justify-between items-start mb-4">
                  <div>
                    <h3 className="text-xl font-bold text-gray-900">Welcome Back</h3>
                    <p className="text-gray-600 text-sm">Sign in to access your account</p>
                  </div>
                  <button
                    onClick={closeAuthModal}
                    className="text-gray-800 hover:text-black transition-colors focus:outline-none focus:ring-2 focus:ring-amber-500 focus:ring-offset-2 rounded-full p-1"
                    aria-label="Close authentication modal"
                  >
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>
                
                {/* Google Sign In Button */}
                <button
                  onClick={() => {
                    closeAuthModal();
                    signInWithGoogle();
                  }}
                  className="w-full flex items-center justify-center px-4 py-3 border-0 rounded-lg transition-all duration-300 font-medium bg-white text-gray-700 border border-gray-300 hover:bg-gray-50"
                >
                  <div className="w-5 h-5 mr-3 flex-shrink-0">
                    <svg viewBox="0 0 24 24" className="w-5 h-5">
                      <path
                        d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
                        fill="#4285F4"
                      />
                      <path
                        d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
                        fill="#34A853"
                      />
                      <path
                        d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
                        fill="#FBBC05"
                      />
                      <path
                        d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
                        fill="#EA4335"
                      />
                    </svg>
                  </div>
                  Sign in with Google
                </button>
                
                <div className="mt-4 text-center text-xs text-gray-500">
                  By signing in, you agree to our Terms of Service.
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Header;